import { FC, PropsWithChildren, forwardRef } from 'react';
import { styled } from '../../utils';
import { TwBaseProps } from '../../types';
import {
  Skeleton as MantineSkeleton,
  SkeletonProps as MantineSkeletonProps
} from '@mantine/core';

export interface SkeletonProps
  extends TwBaseProps,
  PropsWithChildren,
  Pick<MantineSkeletonProps, 'visible' | 'height' | 'width' | 'circle' | 'radius' | 'animate'> {
}

const Root = styled(MantineSkeleton)<MantineSkeletonProps>(() => ({}));

export const Skeleton: FC<SkeletonProps> = forwardRef((props, ref) => {
  return <Root {...props} ref={ref} />;
});
